import { StatusBar } from "expo-status-bar";
import React, { useEffect, useState } from "react";
import {
  Text,
  View,
  TouchableOpacity,
  Image,
  Dimensions,
  TextInput,
  ScrollView,
  Modal,
} from "react-native";
import * as ImagePicker from "expo-image-picker";
import BouncyCheckboxGroup, {
  ICheckboxButton,
} from "react-native-bouncy-checkbox-group";
import BottomNav from "../components/BottomNav";
import { Feather } from "@expo/vector-icons";
import { AntDesign } from "@expo/vector-icons";
import { addProduct, getProduct } from "../services/firestore_product";
import styles from "../styles/AddStyles";

export default function Detail({ navigation, route }) {
  const [name, setName] = useState("");
  const [price, setPrice] = useState(0);
  const [condition, setCondition] = useState(0);
  const [size, setSize] = useState(0);
  const [style, setStyle] = useState([]);
  const [category, setCategory] = useState([]);
  const [detail, setDetail] = useState("");
  const [image, setImage] = useState(null);

  const checkboxStyles = {
    fillColor: "#91B391",
    unfillColor: "white",
    textStyle: {
      textDecorationLine: "none",
    },
  };

  const conditionCheckboxGroup = [
    {
      id: 0,
      text: "",
      ...checkboxStyles,
    },
    {
      id: 1,
      text: "",
      ...checkboxStyles,
    },
    {
      id: 2,
      text: "",
      ...checkboxStyles,
    },
    {
      id: 3,
      text: "",
      ...checkboxStyles,
    },
  ];

  const sizeCheckboxGroup = [
    {
      id: 0,
      text: "L",
      ...checkboxStyles,
    },
    {
      id: 1,
      text: "M",
      ...checkboxStyles,
    },
    {
      id: 2,
      text: "S",
      ...checkboxStyles,
    },
  ];

  useEffect(() => {
    const fetchProductInfo = async () => {
      try {
        const productInfo = await getProduct(route.params.src);
        console.log(productInfo);
        setName(productInfo.name);
        setPrice(productInfo.price);
        const conNum = conditionCheckboxGroup.find(
          (obj) => obj.text === productInfo.condition
        ).id;
        setCondition(conNum);
        const sizeNum = sizeCheckboxGroup.find(
          (obj) => obj.text === productInfo.size
        ).id;
        setSize(sizeNum);
        setStyle(productInfo.style);
        setCategory(productInfo.category);
        setDetail(productInfo.detail);
      } catch (err) {
        console.log(err);
      }
    };

    fetchProductInfo();
  }, []);

  //   
  const renderSelectedStyles = () => {
    return (
      <ScrollView horizontal style={styles.selectedItemsContainer}>
        {style.map((item, index) => (
          <View key={index} style={styles.selectedItem}>
            <Text style={styles.selectedItemText}>{item}</Text>
          </View>
        ))}
      </ScrollView>
    );
  };
  //   
  const renderSelectedCategories = () => {
    return (
      <ScrollView horizontal style={styles.selectedItemsContainer}>
        {category.map((item, index) => (
          <View key={index} style={styles.selectedItem}>
            <Text style={styles.selectedItemText}>{item}</Text>
          </View>
        ))}
      </ScrollView>
    );
  };

  return (
    <View style={styles.container}>
      <StatusBar style="auto" />

      <View style={styles.menuBar}>
        <TouchableOpacity
          onPress={() => navigation.goBack()}
          style={styles.menuIcon}
        >
          <AntDesign name="close" size={28} color="black" />
        </TouchableOpacity>
        <TouchableOpacity style={styles.menuIcon}>
          {/*    */}
          <Text style={styles.menuText}></Text>
        </TouchableOpacity>
      </View>

      <ScrollView style={styles.body}>
        <Image style={styles.pic} source={{ uri: route.params.src }} />

        <View style={styles.hr} />

        <View style={styles.inputBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}></Text>
          </View>
          <TextInput editable={false} value={name} style={styles.input} />
        </View>

        <View style={styles.inputBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}></Text>
          </View>
          <Text>{price} </Text>
        </View>

        <View style={styles.inputBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}></Text>
          </View>
          <BouncyCheckboxGroup
            initial={condition}
            data={conditionCheckboxGroup}
            style={styles.checkbox}
          />
        </View>

        <View style={styles.inputBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}></Text>
          </View>
          <BouncyCheckboxGroup
            initial={size}
            data={sizeCheckboxGroup}
            style={styles.checkbox}
          />
        </View>

        <View style={styles.inputBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}></Text>
          </View>
          <View>{renderSelectedCategories()}</View>
        </View>

        <View style={styles.inputBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}></Text>
          </View>
          <View>{renderSelectedStyles()}</View>
        </View>

        {/* input    !*/}
        <View style={styles.inputAreaBox}>
          <View style={styles.labelBox}>
            <Text style={styles.label}> </Text>
          </View>
          <TextInput
            value={detail}
            editable={false}
            multiline={true}
            placeholder="   :("
            style={styles.inputArea}
          />
        </View>
      </ScrollView>

      <BottomNav navigation={navigation} />
    </View>
  );
}
